import {useEffect, useRef} from 'react';

export const useUpdate = (fn: () => void, deps: any[]) => {
    const count = useRef(0);
    useEffect(() => {
        count.current += 1;
    });
    useEffect(() => {
        if (count.current > 1) {
            fn();
            //window.localStorage.setItem('tags', JSON.stringify(tags));
        }
        // eslint-disable-next-line
    }, deps); //不可变数据
};

